<template>
  <div id="getActivateCode">
    <img class='loginPic' src="../static/img/login.jpg" >
    <div>
      <div id="vims">IP视频交换矩阵激活平台</div>
      <div id="vims2">VIMS-COORDINATOR</div>
    </div>

    <div class="loginCard">
      <div class="transPic"></div>

      <el-form ref="form" :model="form" label-width="80px" >

        <el-form-item id="device" style="font-weight: bold;"label="设备码:" @keyup.enter.native ="onSubmit">
          <el-input v-model="form.device" autoComplete="off"></el-input>
        </el-form-item>


        <el-form-item id="mail" style="font-weight: bold;"label="邮箱:" @keyup.enter.native ="onSubmit">
          <el-input v-model="form.mail" autoComplete="off"></el-input>
        </el-form-item>


        <el-form-item id="user" style="font-weight: bold;"label="姓名:" @keyup.enter.native ="onSubmit">
          <el-input v-model="form.user" autoComplete="off"></el-input>
        </el-form-item>


        <el-form-item id="reason" style="font-weight: bold;"label="理由:" @keyup.enter.native ="onSubmit">
          <el-input type="textarea" :rows="2" v-model="form.reason" autoComplete="off"></el-input>
        </el-form-item>


        <el-form-item id='loginBtn'>
          <el-button  type="primary" @click="onSubmit()">提  交</el-button>
        </el-form-item>

      </el-form>

    </div>



  </div>
</template>

<script>
  import axios from 'axios';


  export default {
    name:'getActivateCode',

    data () {
      return {
        form: {
            device: '',
            mail: '',
            user:'',
            reason:''
        }
      }
    },
    methods: {

      onSubmit: function () {
          var qs = require('qs');
          var self=this;

          axios.post('http://111.230.148.202/ActivateCode/Get_Activate_Code.php', qs.stringify({
            Device_Feature_Code:this.form.device,
            Mail:this.form.mail,
            Name:this.form.user,
            Reason:this.form.reason
          }))
            .then(function (response) {

              if (response['data']['Success'] === false)
              {
                self.$notify.error({
                  title: '失败',
                  message: response.data.Message.content
                });
              }
              else {
                self.$notify.success({
                  title: '成功',
                  message: response.data.Message.content
                });

              }
            })
            .catch(function (error) {
              console.log(error);
            })

      }

    }

  }
</script>

<style>

  .loginPic {
    min-height: 100%;
    min-width: 1024px;
    width: 100%;
    height: auto  !important;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index:-1;

    /*filter: grayscale(0.2);*/
    filter: brightness(0.8);

  }

  .transPic {

    position: relative;
    margin:12px auto;
    width:80vh;
    height:55vh;
    color: #000;


    background-color: white;
    opacity: 0.3;
    z-index: -1;

  }

  #vims {
    text-align: center;
    /*color: greenyellow;*/
    color: #ffe3c3;
    font-weight: bold;
    font-size:65px;
    margin-top:10vh;

    /*font-family: 伯乐字库翩跹体;*/
  }
  #vims2 {
    text-align: center;
    /*color: greenyellow;*/
    color: #ffe3c3;
    font-size:55px;
    margin-top:0vh;
    margin-left: 0.4vh;

    font-family: "DejaVu Math TeX Gyre" ;
  }



  #device {
    margin:0 auto;
    width:55vh;
    margin-top:-51vh;
  }

  #mail {
    margin:0 auto;
    width:55vh;
    margin-top:5vh;
  }

  #user {
    margin:0 auto;
    width:55vh;
    margin-top: 5vh;
    color:red;
  }

  #reason {
    margin:0 auto;
    width:55vh;
    margin-top: 5vh;
    color:red;
  }


  #loginBtn {
    margin:0 auto;
    text-align: center;
    margin-left:-80px;
    margin-top:4vh;
    /*align-content: center;*/
    /*width:30vh;*/
  }



</style>



