<template>
  <div id="getActivateCode">
    <img class='loginPic' src="../static/img/login.jpg" >
    <div>
      <div id="vims">IP视频交换矩阵激活码列表</div>
      <div id="vims2">VIMS-COORDINATOR</div>
    </div>

    <el-table
      :data="list"
      stripe
      highlight-current-row
      style="width: 80%;margin-left: 10vw;margin-top: 3vh;opacity: 0.8;color: black">
      <el-table-column
        type="index"
        >
      </el-table-column>
      <el-table-column
        prop="Name"
        label="姓名"
        >
      </el-table-column>
      <el-table-column
        prop="Mail"
        label="邮箱"
        >
      </el-table-column>
      <el-table-column
        prop="Reason"
        label="理由">
      </el-table-column>

      <el-table-column
        prop="DeviceCode"
        label="设备码">
      </el-table-column>

      <el-table-column
        prop="ActivateCode"
        label="激活码">
      </el-table-column>
    </el-table>



  </div>
</template>

<script>
  import axios from 'axios';


  export default {
    name:'getActivateCode',

    data () {
      return {
        list:[],
      }
    },
    created:function () {
      var self = this;
      axios.get('http://111.230.148.202/ActivateCode/Register_List.php')
        .then(function (response){

            self.list = response.data;

        })
    },
    methods: {

    },

  }
</script>

<style>

  .loginPic {
    min-height: 100%;
    min-width: 1024px;
    width: 100%;
    height: auto  !important;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index:-1;

    /*filter: grayscale(0.2);*/
    filter: brightness(0.8);

  }



  #vims {
    text-align: center;
    /*color: greenyellow;*/
    color: #ffe3c3;
    font-weight: bold;
    font-size:65px;
    margin-top:10vh;

    /*font-family: 伯乐字库翩跹体;*/
  }
  #vims2 {
    text-align: center;
    /*color: greenyellow;*/
    color: #ffe3c3;
    font-size:55px;
    margin-top:0vh;
    margin-left: 0.4vh;

    font-family: "DejaVu Math TeX Gyre" ;
  }







</style>



