import Vue from 'vue'
import Router from 'vue-router'
import registerList from '../components/registerList'
import getActivateCode from '../components/getActivateCode.vue'

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'getActivateCode',
      component: getActivateCode
    },
    {
      path: '/getList',
      name: 'registerList',
      component: registerList
    },

  ]
})



